@import url(https://fonts.googleapis.com/css?family=ZCOOL+XiaoWei);
@import url(https://fonts.googleapis.com/css?family=ZCOOL+XiaoWei);
.BodyWrapper {
    display: grid;
    grid-template-columns: 1fr;
    -webkit-align-content: center;
            align-content: center;
    justify-items: center;
}
@media only screen and (min-width: 320px) {
    .CharacterWrapper {
        margin-top: 40px;
        margin-bottom: 40px;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        grid-row-gap: .5em;
        grid-column-gap: .5em;
    }
}

@media only screen and (min-width: 600px) {
    .CharacterWrapper {
        grid-template-columns: 1fr 1fr 1fr 1fr;
        grid-row-gap: 1.5em;
        grid-column-gap: 1.5em;
    }
}



@media only screen and (min-width: 320px) {
    nav {
        position: fixed;
        top: 0;
        height: 80px;
        width: 100%;
    
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: auto;
        grid-row-gap: .5em;
        justify-items: center;
        -webkit-align-content: center;
                align-content: center;
    
        background: #111;
        color: #fff;
        font-family: 'ZCOOL XiaoWei', serif;
        font-size: 16px;
    }
}

@media only screen and (min-width: 768px) {
    nav {
        grid-template-columns: 1fr 1fr 1fr;
        font-size: 16px;
    }
}


@media only screen and (min-width: 1024px) {
    nav {
        font-size: 20px;
    }
}

@media only screen and (min-width: 1200px) {
    nav {
        font-size: 25px;
    }
}

@media only screen and (min-width: 320px) {
    .jumbotron {
        margin-top: 80px;
        height: 400px;
        width: 100%;
    
        display: grid;
        grid-template-columns: 1fr;
        justify-items: center;
        -webkit-align-items: center;
                align-items: center;
    
        font-family: 'ZCOOL XiaoWei', serif;
        font-size: 20px;
        color: #fff;
    
        background-image: url(/Harry-Potter-Memory-Game/static/media/hogwarts.601a38d5.jpg);
        background-size: cover;
    }
    
    .instructions-div {
        text-align: center;
    }
}

@media only screen and (min-width: 768px) {
    .jumbotron {
        font-size: 25px;
    }
}

@media only screen and (min-width: 1024px) {
    .jumbotron {
        font-size: 30px;
    }
}




.avatar {
    cursor: pointer;
}

@media only screen and (min-width: 768px) {
    .avatar {
        height: 175px;
        width: 175px;
    }
}

